<template>
  <div>
    <HeaderForm @search="setSearch" ref="headerForm" />
    <GrowTable ref="growTableInfo" :search-params="searchParams" />
  </div>
</template>
<script>
import HeaderForm from './components/HeaderForm.vue'
import GrowTable from './components/GrowTable.vue'

export default {
  name: 'GrowthTable',
  components: {
    HeaderForm,
    GrowTable
  },

  data() {
    return {
      searchParams: {}
    }
  },
  methods: {
    setSearch(val) {
      this.searchParams = val
      this.$refs.growTableInfo.currentPage = 1
      this.$refs.growTableInfo.pageSize = 10
      this.$refs.growTableInfo.getTableDataFun(val)
    },
    resetSearch() {
      if (this.$store.state.user.bus_role === 'hm') {
        this.$refs.growTableInfo.activeName = 'un_send'
      }
      if (this.$store.state.user.bus_role === 'doctor') {
        this.$refs.growTableInfo.activeName = 'tested'
      }
      if (this.$store.state.user.bus_role === 'dietician') {
        this.$refs.growTableInfo.activeName = 'all'
      }
      this.$refs.headerForm.resetForm('growForm')
    }
  },
  // 从非发育量表详情页返回时，重置搜索条件,从发育量表详情页返回时，基于当前搜索条件重新查询
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (!from.path.includes('/grow-table/see-detail')) {
        vm.resetSearch()
      } else {
        vm.$refs.headerForm.handleSearch()
      }
    })
  }
}
</script>
