<template>
  <div class="grow-table" style="margin-top:10px;">
    <el-tabs
      v-model="activeName"
      class="professional-role__header__tabs"
      @tab-click="getStatusList"
    >
      <el-tab-pane
        v-for="{ label, name, num } in tabArr"
        :key="name"
        :name="name"
      >
        <span slot="label" class="professional-role__header__tabs__slot-label"
          ><el-badge v-if="num > 0" :value="num" class="item" />{{
            label
          }}</span
        >
      </el-tab-pane>
    </el-tabs>
    <div class="grow-table__header">服务中的宝宝列表</div>
    <el-table class="grow-table__list" :data="tableDate">
      <el-table-column
        align="center"
        label="序号"
        width="80"
        prop="num"
        fixed
      />
      <el-table-column align="center" label="宝宝" min-width="120" prop="name">
        <template slot-scope="scope">
          <p>{{ scope.row.name }}</p>
          <p>{{ scope.row.babyId || scope.row.id }}</p>
        </template>
      </el-table-column>
      <el-table-column align="center" label="用户" min-width="180" prop="user">
        <template slot-scope="scope">
          <div class="user-avatar">
            <el-avatar :src="scope.row.avatar" :size="28" />{{ scope.row.user }}
            <svg-icon v-if="scope.row.is_replace" icon-class="replace" />
          </div>
          <div>{{ scope.row.userId }}</div>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="出生日期"
        min-width="100"
        prop="birthday"
      />
      <el-table-column
        align="center"
        label="月龄"
        min-width="80"
        prop="monthAge"
      />
      <el-table-column align="center" label="宝宝备注" width="120">
        <template slot="header">
          <span>宝宝备注</span>
          <el-tooltip
            class="item color-576277"
            effect="dark"
            content="接诊台宝宝资料处添加的备注"
            placement="top"
          >
            <i class="el-icon-info" />
          </el-tooltip>
        </template>
        <template slot-scope="scope">
          <div class="baby-note">
            {{ scope.row.babyNote }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="当前服务套餐"
        min-width="120"
        prop="package"
      />
      <el-table-column
        align="center"
        label="当前服务时间"
        width="210"
        prop="serviceTime"
      />
      <el-table-column
        align="center"
        label="是否有待发送"
        min-width="80"
        prop="sent"
      />
      <el-table-column
        align="center"
        label="是否有待反馈"
        min-width="80"
        prop="feedback"
      />
      <el-table-column align="center" label="操作" fixed="right" width="120">
        <template slot-scope="scope">
          <div>
            <el-button type="text" @click="handleSee(scope)">查看</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        :current-page="currentPage"
        :page-sizes="[10, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import { getGrowthList } from '@/api/grow-table'
export default {
  props: {
    searchParams: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      addLoading: false,
      addMembers: [],
      members: [],
      loading: false,
      tableDate: [],
      currentPage: 1,
      pageSize: 10,
      count: 0,
      activeName: 'un_send',
      tabArr: [
        {
          label: '待发送',
          name: 'un_send',
          num: 0
        },
        {
          label: '待反馈',
          name: 'tested',
          num: 0
        },
        {
          label: '全部',
          name: 'all',
          num: 0
        }
      ]
    }
  },
  created() {
    if (this.$store.state.user.bus_role === 'hm') this.activeName = 'un_send'
    if (this.$store.state.user.bus_role === 'doctor') this.activeName = 'tested'
    if (this.$store.state.user.bus_role === 'dietician') this.activeName = 'all'
    this.getTableDataFun()
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val
      this.getTableDataFun()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getTableDataFun()
    },
    handleSee({ row }) {
      this.$router.push({
        name: 'growListDetail',
        params: { id: row._id }
      })
    },
    getTableDataFun(search) {
      const params = {
        page_size: this.pageSize,
        current_page: this.currentPage,
        baby_grow_info_status: this.activeName,
        service_item: 'baby_growth',
        show_replace: 1
      }
      if (this.searchParams) {
        if (this.searchParams.name) params.baby_name = this.searchParams.name
        if (this.searchParams.package)
          params.fd_service_type = this.searchParams.package
        if (this.searchParams.user) params.user_name = this.searchParams.user
        if (this.searchParams.userId) params.user_id = this.searchParams.userId
        if (this.searchParams.babyId) params.baby_id = this.searchParams.babyId
      }
      if (search) {
        if (search.name) params.baby_name = search.name
        if (search.package) params.fd_service_type = search.package
        if (search.user) params.user_name = search.user
        if (search.userId) params.user_id = search.userId
        if (search.babyId) params.baby_id = search.babyId
      }
      getGrowthList(params).then(res => {
        if (res && res.data) {
          this.count = res.data.count
          this.tabArr[0].num = res.data.baby_grow_info.un_send_count
          this.tabArr[1].num = res.data.baby_grow_info.suggest_count
          this.tableDate = res.data.list.map(item => {
            return {
              num: item.index,
              name: item.name,
              user: item.user_info.nickname,
              avatar: item.user_info.avatar,
              userId: item.user_info._id,
              birthday: this.formatTime(item.birthday, '{y}-{m}-{d}') || '--',
              monthAge: item.age_of_month || '--',
              babyNote: item.remark || '宝宝备注',
              package: item.fd_service_type_info.name,
              serviceTime:
                item.fd_contract_info.started_at &&
                item.fd_contract_info.ended_at
                  ? this.formatTime(
                      item.fd_contract_info.started_at,
                      '{y}-{m}-{d}'
                    ) +
                    '到' +
                    this.formatTime(
                      item.fd_contract_info.ended_at,
                      '{y}-{m}-{d}'
                    )
                  : '--',
              sent: item.is_un_send === 'no' ? '否' : '是',
              feedback: item.is_suggest === 'no' ? '否' : '是',
              id: item.id,
              _id: item._id,
              babyId: item.babyId,
              is_replace: item.is_replace
            }
          })
        } else {
          this.tableDate = []
          this.count = 0
        }
      })
    },
    getStatusList() {
      this.getTableDataFun()
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.grow-table {
  &__header {
    padding: 0 20px;
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    font-family: PingFangSC-Medium;
    font-weight: 600;
    color: #17233d;
    background: #f5f7fa;
    letter-spacing: 0;
  }
  &__list {
    .el-table__header th {
      color: #17233d;
      letter-spacing: 0;
    }
  }
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  .user-avatar {
    display: inline-block;
    .el-avatar {
      vertical-align: middle;
    }
  }
  .baby-note {
    display: -webkit-box;
    overflow: hidden;
    max-height: 2.4em;
    line-height: 1.2em;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  .professional-role__header__tabs__slot-label {
    .el-badge {
      position: absolute;
      right: 0;
    }
  }
}
</style>
