<template>
  <div class="grow-search">
    <el-form
      ref="growForm"
      label-position="left"
      :model="growForm"
      class="search__form"
      label-width="80px"
      inline
    >
      <el-form-item label="宝宝乳名" prop="name">
        <el-input
          v-model="growForm.name"
          placeholder="请输入宝宝乳名"
          clearable
          size="mini"
        />
      </el-form-item>
      <el-form-item label="宝宝ID" class="search-item" prop="babyId">
        <el-input
          v-model="growForm.babyId"
          placeholder="请输入宝宝ID"
          size="mini"
        />
      </el-form-item>
      <el-form-item label="套餐名称" prop="package">
        <service-package v-model="growForm.package"></service-package>
      </el-form-item>
      <el-form-item label="用户" prop="user">
        <el-input
          v-model="growForm.user"
          placeholder="请输入用户昵称或备注名"
          size="mini"
          clearable
        />
      </el-form-item>
      <br />
      <el-form-item label="用户ID" prop="userId">
        <el-input
          v-model="growForm.userId"
          placeholder="请输入用户ID"
          size="mini"
          clearable
          style="margin-right: 50px;"
        />
        <el-button type="primary" size="mini" @click="handleSearch"
          >搜索</el-button
        >
        <el-button
          type="primary"
          plain
          size="mini"
          @click="resetForm('growForm')"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import ServicePackage from '@/components/SearchItem/ServicePackage'

export default {
  data() {
    return {
      growForm: {
        name: '',
        package: '',
        user: '',
        userId: '',
        babyId: ''
      }
    }
  },
  components: {
    ServicePackage
  },
  methods: {
    handleSearch() {
      this.$refs['growForm'].validate(valid => {
        if (valid) {
          this.$emit('search', this.growForm)
        } else {
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.$emit('search', this.growForm)
    }
  }
}
</script>

<style lang="scss" scoped>
.grow-search {
  .search__form {
    .el-form-item:not(:last-child) {
      margin-right: 70px;
    }
    /deep/.el-input {
      width: 220px;
    }
  }
}
</style>
